import { InputText } from "primereact/inputtext";
import { PrimaryButton } from "components/public/buttons";
import React, { useState } from "react";
import { inject, observer } from "mobx-react";
import styled from "styled-components";
import { Dialog } from "primereact/dialog";
import { SNIPPETS } from "consts/styles";
import { sendForm } from "api/forms";

const FreeOrderModal = inject('userStore')(observer(({ userStore, visible, onHide }) => {

    const [ name, setName ] = useState(userStore.user.name);
    const [ phone, setPhone ] = useState(userStore.user.phone);
    const [ email, setEmail ] = useState(userStore.user.email);
    const [ region, setRegion ] = useState('');
    const [ maxPrice, setMaxPrice ] = useState('');
    const [ roomCount, setRoomCount ] = useState('');
    const [ error, setError ] = useState('');

    let isLoading = false;

    function sendFormClick() {
        if (!isLoading && name.trim() && (email.trim() || phone.trim()) && maxPrice.trim()) {
            isLoading = true;
            sendForm({
                type: 'Замовлення тестового підбору',
                name,
                email,
                phone,
                region,
                roomCount,
                maxPrice
            }).finally(() => {
                isLoading = false;
            });
            onHide();
        } else setError('Заповніть поля корректними даними')
    }

    return (
        <Modal
            onHide={onHide}
            visible={visible}
            header={<Header>Замовлення безкоштовного персонального підбору</Header>}
        >
            <div className="p-inputgroup">
                <span className="p-inputgroup-addon">
                    <i className='bx bx-user'/>
                </span>
                <InputText
                    value={name}
                    type="text"
                    onInput={e => setName(e.target.value)}
                    placeholder="Як вас звати?"
                />
            </div>
            <div className="p-inputgroup">
                <span className="p-inputgroup-addon">@</span>
                <InputText
                    value={email}
                    type="email"
                    onInput={e => setEmail(e.target.value)}
                    placeholder="email"
                />
            </div>
            <div className="p-inputgroup">
                <span className="p-inputgroup-addon">
                    <i className="bx bx-phone" />
                </span>
                <InputText
                    value={phone}
                    type="phone"
                    onInput={e => setPhone(e.target.value)}
                    placeholder="Номер телефону"
                />
            </div>
            <div className="p-inputgroup">
                <span className="p-inputgroup-addon">
                    <i className='bx bx-money'/>
                </span>
                <InputText
                    value={maxPrice}
                    type="text"
                    onInput={e => setMaxPrice(e.target.value)}
                    placeholder="Максимальна ціна"
                />
            </div>
            <div className="p-inputgroup">
                <span className="p-inputgroup-addon">
                    <i className='bx bx-map'/>
                </span>
                <InputText
                    value={region}
                    type="text"
                    onInput={e => setRegion(e.target.value)}
                    placeholder="Бажаний район"
                />
            </div>
            <div className="p-inputgroup">
                <span className="p-inputgroup-addon">
                    <i className='bx bx-home'/>
                </span>
                <InputText
                    value={roomCount}
                    type="text"
                    onInput={e => setRoomCount(e.target.value)}
                    placeholder="Кількість кімнат"
                />
            </div>

            {error && <p style={{color: 'red'}}>{error}</p>}
            <PrimaryButton onClick={sendFormClick}>Замовити</PrimaryButton>
        </Modal>
    )
}));

export default FreeOrderModal;

const Modal = styled(Dialog)`
  color: ${props => props.theme.colors.text.important};
  max-width: 500px;
  .p-dialog-header-close {
    margin-left: 20px;
    ${SNIPPETS.noOutline};
  }
  
  .p-inputgroup {
    margin: 10px auto;
    input {
      ${SNIPPETS.noOutline};
    }
  }
  button {
    width: 100%;
  }
  
  .footer {
    display: flex;  
  }
  .names {
    display: grid;
    input {${SNIPPETS.noOutline};}
  }
  
  .p-dropdown {
    width: 100%;
  }
  .p-inputnumber {
    width: 100%;
  }
`;

const Header = styled.h3`
  font-size: 2rem;
  line-height: 1.1;
  font-weight: 700;
  margin: 30px 0 0 0;
  display: flex;
  color: ${props => props.theme.colors.text.important};
  img {
    height: 2rem;
    margin: auto;
    padding-left: 10px;
  }
`;